<template>
  <div class="songs-container">
    <div class="tab-bar">
      <span class="item">全部</span>
      <span class="item">华语</span>
      <span class="item">欧美</span>
      <span class="item">日本</span>
      <span class="item">韩国</span>
    </div>
    <!-- 底部的table -->
    <table class="el-table playlit-table">
      <thead>
        <th></th>
        <th></th>
        <th>音乐标题</th>
        <th>歌手</th>
        <th>专辑</th>
        <th>时长</th>
      </thead>
      <tbody>
        <tr class="el-table__row" v-for="(item, index) in songs" :key="index">
          <td>{{ index + 1 }}</td>
          <td>
            <div class="img-wrap" @click="playMusic(item.id)">
              <img :src="item.album.picUrl" alt="" />
              <span class="iconfont icon-play"><svg t="1623394163267" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="10143" width="200" height="200"><path d="M725.632 827.904c-63.104 42.88-136.832 65.408-213.504 65.408-210.304 0-381.312-171.008-381.312-381.184 0-75.52 22.016-148.608 63.744-211.072 8.576-12.672 4.992-29.952-7.68-38.528-12.672-8.576-29.952-4.992-38.528 7.68-47.872 71.68-73.088 155.392-73.088 241.92 0 240.768 195.968 436.608 436.864 436.608 87.68 0 172.416-25.856 244.736-74.88 12.672-8.576 16-25.856 7.424-38.656-8.704-12.544-25.984-15.872-38.656-7.296zM512 75.136c-93.312 0-182.272 28.928-257.408 83.84-12.416 9.088-15.104 26.496-6.016 38.784 9.088 12.416 26.496 15.104 38.784 6.016 65.536-47.744 143.232-73.088 224.64-73.088 210.176 0 381.184 171.136 381.184 381.312 0 81.408-25.216 159.104-73.088 224.512-9.088 12.416-6.272 29.824 6.016 38.784 4.864 3.712 10.624 5.376 16.384 5.376 8.576 0 17.024-3.968 22.4-11.52 54.912-75.136 83.84-164.096 83.84-257.28C948.864 271.104 753.024 75.136 512 75.136z m-84.864 611.968c4.864 0 9.6-1.152 13.952-3.84l251.904-145.408c8.576-4.864 13.824-14.08 13.824-24.064 0-9.984-5.248-19.072-13.824-24.064L441.216 344.32c-8.704-4.864-19.2-4.864-27.904 0-8.576 4.864-13.824 14.08-13.824 24.064V659.2c0 9.984 5.248 19.072 13.824 24.064 4.224 2.56 9.088 3.84 13.824 3.84z m27.904-270.592l168.576 97.28-168.576 97.28v-194.56z" p-id="10144" fill="#d81e06"></path></svg></span>
            </div>
          </td>
          <td>
            <div class="song-wrap">
              <div class="name-wrap">
                <span>{{ item.name }}</span>
                <span class="iconfont icon-mv"></span>
              </div>
            </div>
          </td>
          <td>{{ item.album.artists[0].name }}</td>
          <td>{{ item.album.name }}</td>
          <td>{{ item.duration | TimeFormat }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { getSongs } from "@/network/songs.js";
import { getMusic } from "@/network/newmusic.js";
import { TimeForm } from "@/utils/TimeForm.js";
export default {
  name: "Songs",
  data() {
    return {
      songs: [],
      config: {
        type: 0,
      },
    };
  },
  mounted() {
    // console.log(getSongs());
    this.songsHandle();
    // throttle(this.songsHandle,1000)
  },
  methods: {
    // 保存最新歌曲数据
    songsHandle() {
      getSongs(this.config)
        .then((value) => {
          this.songs = value.data.data.slice(0, 30);
          // console.log(this.songs);
        })
        .catch((err) => {});
    },

    // 点击播放音乐
    playMusic(id) {
      // console.log(id);
      getMusic(id)
        .then((value) => {
          // console.log(value);
          // this.$parent.musicUrl = value.data.data[0].url;
          let url = value.data.data[0].url;
          this.bus.$emit("ReceiveMessage", url);
          this.$parent.musicUrl = value.data.data[0].url;
        })
        .catch((err) => {});
    },
  },
  // filters: {
  //   songTime: function (value) {
  //     let seconds = parseInt(value / 60000);
  //     let mins = parseInt((value / 1000) % 60);
  //     seconds = seconds > 10 ? seconds : "0" + seconds;
  //     mins = mins > 10 ? mins : mins + "0";
  //     return `${seconds}:${mins}`;
  //   },
  // },
};
</script>

<style>
.songs-container .tab-bar {
  display: flex;
  justify-content: flex-end;
}

.songs-container .tab-bar .item {
  font-size: 15px;
  color: gray;
  margin-right: 20px;
  cursor: pointer;
}

.songs-container .tab-bar .item.active {
  color: #dd6d60;
}

.songs-container .song-wrap > span {
  margin-top: 20px;
  display: inline-block;
  color: #bebebe;
}

.songs-container .song-wrap .icon-mv {
  padding-left: 5px;
  color: #dd6d60;
}

.songs-container .img-wrap {
  position: relative;
  width: 70px;
  height: 70px;
}

.songs-container .img-wrap img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.songs-container .img-wrap .icon-play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 25px;
  height: 25px;
  color: #dd6d60;
  font-size: 12px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.8);
}

.songs-container .img-wrap .icon-play::before {
  transform: translateX(1px);
}
</style>